import React,{useEffect,useState} from 'react';

import './dashboard.css';
function Card(){
  const  [coviddata, setcoviddata] = useState([]);
    const getCovid=async()=>{
        try{
        const fetchData=await fetch("https://api.covid19india.org/data.json");
const actualData=await fetchData.json();
setcoviddata(actualData.statewise[0]);
        }catch(err){
            console.log(err);
        }
    }
    useEffect(()=>{
getCovid();
    },[]);
return(

    <div className="back">
<div className="covid"><h1>Covid Cases</h1></div>

    
<div className="container">
  <div className="row">

    <div className="col">
    <div className="card">
  <div className="card-body">
    <h5 className="card-title"><span>Our</span> Country</h5>
    
    <p className="card-text">India</p>
    
  </div>
</div>
    </div>
 
    <div className="col">
    <div className="card" style={{
      backgroundColor:"rgb(255, 0, 36)"
    }}>
  <div className="card-body">
    <h5 className="card-title"><span>Total</span> Deaths</h5>
    
    <p className="card-text">{coviddata.deaths}</p>
    
  </div>
</div>
    </div>

    <div className="col">
    <div className="card"  style={{
      backgroundColor:"rgb(0, 141, 255)"
    }}>
  <div className="card-body">
    <h5 className="card-title"><span>Total</span> Confirmed</h5>
    
    <p className="card-text">{coviddata.confirmed}</p>
    
  </div>
</div>
    </div>
    <div className="col">
    <div className="card">
  <div className="card-body">
    <h5 className="card-title"><span>Total</span> Recovered</h5>
    
    <p className="card-text"> {coviddata.recovered}</p>
    
  </div>
</div>
    </div>
    <div className="col">
    <div className="card">
  <div className="card-body">
    <h5 className="card-title"><span>Total</span> Active</h5>
    
    <p className="card-text">{coviddata.active}</p>
    
  </div>
</div>
    </div>
    <div className="col">
    <div className="card">
  <div className="card-body">
    <h5 className="card-title"><span>Last</span> Updated</h5>
    
    <p className="card-text">{coviddata.lastupdatedtime}</p>
    
  </div>
</div>
    </div>
    
    
  </div>
</div>
    </div>

   
)

}
export default Card;